
import Vue, {defineComponent} from "vue";
import Header from "./header.vue";
import NavBar from "./navbar.vue";
import Footer from "./footer.vue";
import axios from "axios";


import {nextTick} from "@vue/runtime-core";

import './style/default.less'

export default defineComponent ({
  name: 'dashboard',
  components: {
    "dash-header": Header,
    navbar: NavBar,
    "dash-footer": Footer
  },
  props: {
    version: String,
    enableByConfig: Boolean,
    configUrl: String,
    infoUrl: String,
    logoutUrl: String,
    legacyAuth: Boolean
  },
  data() {
    return {
      user: null,
      error: null,
      apps: [
        {
          Enabled: true,
          Name: "Dashboard",
          Url: "",
          Icon: "gauge"
        }
      ],
      loading: true,
      enabled: true,
      showNav: false,
      hideNav: false
    };
  },
  mounted() {
    console.log('dashboard created')
    this.authorize(() => {
      if (this.enabled) {
        this.getConfig();
      } else {
        this.loading = false;
      }
    });
  },

  computed: {
    userFullName() {
      return this.user ? `${this.user.FirstName} ${this.user.LastName}` : "";
    },
    logoUrl() {
      let url = this.apps[0].Url;
      return url && this.user
        ? `${url}/Root/Logo?company=${this.user.CompanyNo}&employee=${this.user.EmployeeNo}`
        : "";
    }
  },
  methods: {
    authorize(cb) {
      console.log('dashboard authorize')

      let remoteConfig = {
        method: process.env.VUE_APP_AXIOSHOST ? 'get' : 'post',
        url: process.env.VUE_APP_AXIOSHOST ? "http://localhost:3000/Auth/GetInfo" : "/Auth/GetInfo" //https://payadvance.testaffinitylogon.com/Auth
      }

      axios(remoteConfig)
      // axios.get(url)
        .then(resp => {
          let user = (Affinity.user = this.legacyAuth
            ? {
                FirstName: resp.data.Firstname,
                LastName: resp.data.Lastname,
                PayPoint: resp.data.PayPoint,
                CompanyNo: resp.data.CompanyNo,
                EmployeeNo: resp.data.EmployeeNo
              }
            : resp.data);

          Affinity.companyNumber = resp.data.CompanyNo;
          Affinity.employeeNumber = resp.data.EmployeeNo;

          this.enabled = !this.enableByConfig || user.UseDashboard;

          if (cb) cb();
        })
        .catch((err) => {
          console.log('err', err)
          this.error = "Unable to load the page.";
          this.loading = false;
        });
    },
    getConfig() {
      let remoteConfig = {
        method: process.env.VUE_APP_AXIOSHOST ? 'get' : 'post',
        url: process.env.VUE_APP_AXIOSHOST ? "http://localhost:3000/Config/Dashboard" : "/Config/Dashboard"//"https://payadvance.testaffinitylogon.com/Config/Dashboard"
      }

      axios(remoteConfig)
        // .get(this.configUrl)
        .then(resp => {
          let config = resp.data;

          this.user = Affinity.user;

          this.apps[0].Url = config.Url;

          if (config.Apps) {
            config.Apps.forEach(app => {
              this.apps.push(app.Link);
            });
          }

          if (config.Forms && this.user.EmployeeNo <= 5000000) {
            config.Forms.forEach(form => {
              if (form.Enabled) this.apps.push(form);
            });
          }

          this.loading = false;

          nextTick(() => {
            this.$emit("ready", config);
          });
        })
        .catch((err) => {
          console.log('err', err)
          this.error = "Unable to load the page.";
        })
        .then(() => {
          this.loading = false;
        });
    },
    reloadPage() {
      window.location.reload();
    },
    onNavToggle(mobile) {
      if (mobile) this.showNav = !this.showNav;
      else this.hideNav = !this.hideNav;
    },
    onNavHide() {
      this.showNav = false;
    }
  },

});
