
// error message
export const GENERAL_ERROR = 'Sorry - Pay Advance is currently unavailable.';

export const PRO_ERROR = {
    PERIOD_OUT_OF_RANGE: 'period-out-of-range',
    OVER_THE_LIMIT: 'over-the-limit' ,
    FREQUENT_LIMIT: 'frequent-limit' ,
    UNPAID_PREVIOUS_PERIOD: 'unpaid-previous-period'
}

export function getErrorMessageFromCode(errorCode: string, nextPayCycleDate: string, lastRequestDate: string): string {
    switch (errorCode) {
        case PRO_ERROR.PERIOD_OUT_OF_RANGE:
            return `Your salary has already been paid in advance. You can use Pay Advance again between ${nextPayCycleDate} and your next pay day.`;
        case PRO_ERROR.OVER_THE_LIMIT:
            return `Amount requested greater than amount available.`;
        case PRO_ERROR.FREQUENT_LIMIT:
            return `You can use Pay Advance once per pay cycle. Your last request was on ${lastRequestDate}. Pay Advance will be available again on ${nextPayCycleDate}.`;
        case PRO_ERROR.UNPAID_PREVIOUS_PERIOD:
            return `Pay Advance will be available once your pay has been banked.`;
        default:
            return GENERAL_ERROR;
    }
}