<script >
    export default {
        props: {
            title: String,
            logoUrl: String
        }
    }
</script>

<template>
    <div class="dash-header">
        <div id="mobile-nav-toggle" class="dash-menu-toggle" @mousedown.stop="">
            <span class="icon-list" @click="$emit('navtoggle', true)"></span>
        </div>
        <div id="desktop-nav-toggle" class="dash-menu-toggle">
            <span class="icon-list" @click="$emit('navtoggle', false)"></span>
        </div>
        <div class="dash-header-title displayname">{{ title }}</div>
        <div class="dash-header-logo">
            <img :src="logoUrl" />
        </div>
    </div>
</template>

<style lang="less">
@import "./style/themes/dashboard.theme.default.less";
@import "./style/themes/ui.functions.less";
@import "./style/themes/ui.font.icons.less";
.dash-header {
  display: block;
  position: fixed;
  width: 100%;
  z-index: 100;
  height: @header-height;
  color: @header-text-color;
  background: @header-bg-color;
  .boxShadow(0,3px,3px,0,0,0,0.3);

  #desktop-nav-toggle {
    display: none;
  }

  .dash-menu-toggle {
    display: inline-block;
    vertical-align: top;
    line-height: 60px;
    font-size: 26px;
    width: 60px;
    text-align: center;

    span {
      cursor: pointer;
      padding: 7px;
      border-radius: 50%;
      color: #777;
      transition: color linear .2s;
    }
  }

  .dash-header-title {
    display: inline-block;
    //padding: ((@header-height / 2) - (@header-title-size / 2) - 5px) 0 0 0;
    //height: @header-height;
    font-size: @header-title-size;
    line-height: @header-height;
    font-weight: normal;
    font-style: italic;
    vertical-align: top;
  }

  .dash-header-sub-title {
    display: inline-block;
    padding: 0px 0 0 10px;
    //height: @header-title-size;
    line-height: @header-height;
    font-size: ((@header-title-size / 3) * 2);
    font-weight: normal;
  }

  @logo: "@{logo-image}";

  .dash-header-logo {
    position: absolute;
    top: (@header-height / 5);
    right: (@header-height / 5);
    background-image: url(@logo);
    background-position: 1000px 1000px;
    background-repeat: no-repeat;

    img {
      max-height: ((@header-height / 5) * 3);
    }
  }
