<script lang="ts">
import Vue, {defineComponent} from 'vue';
import URI from 'urijs';
// import TweenMax from 'TweenMax';
import gsap from 'gsap';
import {nextTick} from "@vue/runtime-core";
// import Affinity from "../../src/shims-affinity";

export default defineComponent({
  name: 'nav-item',
  props: {
    data: {
      type: Object,
      default() {
        return {};
      }
    },
    host: String,
    isOwned: Boolean
  },
  data() {
    return {
      expanded: false,
      expanding: false
    }
  },
  mounted() {
    this.expanded = this.selected && this.hasMenu;
    this.expanding = this.expanded;
  },
  computed: {
    target() {
      return this.data.External ? this.data.Name || '_blank' : '_self';
    },
    selected() {
      var path = new URI(this.url).path().toLowerCase();
      return (!this.isOwned && Affinity.app == this.data.Name)
          || (path !== '/' && path === window.location.pathname.toLowerCase())
          || (path === '/cleverforms/inbox' && window.location.pathname.toLowerCase() === '/inbox')
          || (path === '/cleverforms/template' && window.location.pathname.toLowerCase() === '/template')
          || (path === '/cleverforms/admin' && window.location.pathname.toLowerCase() === '/admin');
    },
    hasMenu() {
      return this.data.Menu && this.data.Menu.length > 0;
    },
    url() {
      return this.hasMenu ? 'javascript:void(0);' : this.data.Url;
    }
  },
  methods: {
    toggleMenu() {
      if (!this.hasMenu) return;

      this.expanding = !this.expanded;

      if (this.expanding) {
        this.expanded = true;
        nextTick(() => {
          gsap.from(this.$refs.menu, {
            duration: 0.25,
            height: 0
          });
        })
      } else gsap.to(this.$refs.menu, {
        duration: 0.25,
        height: 0,
        onComplete: () => {
          this.expanded = false;
        }
      });
    }
  },

})
</script>

<template>
  <a class="dash-nav-item" :href="url" :target="target"
     :class="{ selected: selected, 'has-sub-nav': hasMenu, expanded: !expanding }">
    <div class="dash-nav-item-inner" @click="toggleMenu">
      <span class="sub-nav-icon icon-arrow-line-small-up"></span>
      <span :class="'icon icon-' + data.Icon"></span>
      <span class="label">{{ data.Text || data.Name }}</span>
    </div>
    <div ref="menu" class="sub-nav" v-if="hasMenu && expanded">
      <nav-item v-for="(subItem, index) in data.Menu" :data="subItem" :is-owned="true" :key="index"></nav-item>
    </div>
  </a>
</template>
